.Button {
    align-items: center;
    border-color: transparent;
    border-style: solid;
    border-width: 1px;
    border-radius: 0.25rem;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-flex;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 0.875rem;
    font-weight: 500;
    justify-content: center;
    min-height: 2rem;
    min-width: 4rem;
    outline: 0;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    user-select: none;
    vertical-align: middle;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
  }
  
  .Button__label {
    align-items: inherit;
    display: inherit;
    justify-content: inherit;
    width: 100%;
  }
  
  /* TEXT BUTTON STYLES */
  
  .Button--primary, .Button--grey {
    background-color: transparent;
    padding: 0.5rem;
  }
  
  /* CONTAINED BUTTON STYLES */
  
  .Button--primary--contained, .Button--grey--contained {
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
    padding: 0.5rem 1rem;
  }
  
  .Button--primary--contained:active, .Button--grey--contained:active {
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
  }
  
  /* PRIMARY TEXT BUTTON STYLES */
  
  .Button--primary {
    color: #3f51b5;
    &:focus, &:hover {
      background-color: #f0f1f9;
    }
  }
  
  /* PRIMARY CONTAINED BUTTON STYLES */
  
  .Button--primary--contained {
    background-color: #3f51b5;
    border-color: transparent;
    color: #ffffff;
  }
  
  .Button--contained.Button--primary {
    &:focus, &:hover {
      background-color: #303f9f;
    }
  }
  
  /* GREY TEXT BUTTON STYLES */
  
  .Button--grey {
    color: #313131;
    &:focus, &:hover {
      background-color: #ebebeb;
    }
  }
  
  /* GREY CONTAINED BUTTON STYLES */
  
  .Button--grey--contained {
    background-color: #e0e0e0;
    color: #313131;
    &:focus, &:hover {
      background-color: #d5d5d5;
    }
  }