/* Custom Stylesheet */
/**
 * Use this file to override Materialize files so you can update
 * the core Materialize files in the future
 *
 * Made By MaterializeCSS.com
 */

 .navbartext {
    color: white;
  }
  
  .navbarcolor {
    background-color: black;
  }
  
  .navbarlogo {
    float: left;
  }
  
  nav {
    ul a, .brand-logo {
      color: #444;
    }
  }
  
  p {
    line-height: 2rem;
  }
  
  .sidenav-trigger {
    color: #26a69a;
  }
  
  .parallax-container {
    min-height: 380px;
    line-height: 0;
    height: auto;
    color: rgba(255, 255, 255, 0.9);
    .section {
      width: 100%;
    }
  }
  
  @media only screen and (max-width: 992px) {
    .parallax-container .section {
      position: absolute;
      top: 40%;
    }
    #index-banner .section {
      top: 10%;
    }
  }
  
  @media only screen and (max-width: 600px) {
    #index-banner .section {
      top: 0;
    }
  }
  
  .icon-block {
    padding: 0 15px;
    .material-icons {
      font-size: inherit;
    }
  }
  
  footer.page-footer {
    margin: 0;
  }